body {
  background: #efefef; }

.site-wrapper {
  background: white;
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 50px; }

.clearfix:after {
  content: "";
  display: table;
  clear: both; }

header {
  padding: 20px; }

header h1 {
  margin-bottom: 0;
  font-size: 44px; }

header h2 {
  color: silver;
  font-weight: normal;
  margin: 0; }

footer {
  padding: 20px; }

a {
  color: black;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

.navigations {
  display: flex;
  flex-direction: column; }

.navigation_bar {
  display: flex;
  padding: 0;
  margin: 20px;
  margin-bottom: 0;
  border-top: 1px dotted silver;
  border-bottom: 1px dotted silver;
  justify-content: space-between; }

.navigation_bar + .navigation_bar {
  margin-top: 0;
  border-top: none; }

.navigation {
  margin: 0;
  padding: 0;
  display: flex; }

.navigation__item {
  list-style-type: none; }
  .navigation__item:hover {
    background: #efefef; }

.navigation__link {
  text-decoration: none;
  padding: 10px 15px;
  display: block; }

.navigation__link--secondary {
  padding: 6px 12px; }

.navigation__item--active {
  background: #efefef; }
  .navigation__item--active a {
    color: black; }

#posts {
  margin-top: 40px;
  display: flex;
  flex-direction: column; }

.post + .post {
  border-top: 1px solid #efefef; }

.post {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  margin: 10px 0; }
  @media all and (max-width: 680px) {
    .post {
      flex-direction: column; } }

.post__image img {
  width: 150px;
  border: 1px solid #efefef; }
  @media all and (max-width: 680px) {
    .post__image img {
      width: 300px;
      height: auto;
      max-width: 100%; } }

.post__content {
  width: calc(100% - 180px); }
  @media all and (max-width: 680px) {
    .post__content {
      width: 100%; } }

.post__title {
  margin-top: 0;
  line-height: 1.2; }

.post__excerpt {
  color: #888;
  line-height: 1.4; }

.post__metadata {
  font-size: 14px; }
  .post__metadata .source {
    color: #5F5F5F;
    text-transform: uppercase; }
  .post__metadata .info {
    padding: 3px 6px;
    border-radius: 3px;
    border: 1px solid #ffa57b;
    color: #ffa57b; }
